import { options } from "../options";
import { imagesCollection } from "../imagesCollection";
import Masonry, {ResponsiveMasonry} from 'react-responsive-masonry';
import Carousel, { Modal, ModalGateway } from "react-images";
import React, { Component, Fragment } from 'react'

const columnsCountBreakPoints = { 350: 1, 750: 2, 900: 3 };

type State = {
  selectedIndex?: number,
  lightboxIsOpen: boolean,
}

export default class Wrapper extends Component<State> {
  state = {
    selectedIndex: 0,
    lightboxIsOpen: false,
  }
  toggleLightbox = (selectedIndex: number) => {
    this.setState(state => ({
      lightboxIsOpen: !state.lightboxIsOpen,
      selectedIndex,
    }))
  }
  render() {
    const { images, isLoading } = this.props
    const { selectedIndex, lightboxIsOpen } = this.state
    return (
      <Fragment>
        {!isLoading ? (
          <ResponsiveMasonry columnsCountBreakPoints={columnsCountBreakPoints}>
        <Masonry gutter="1rem">
            {imagesCollection.map(({ desc, thumbnail }, j) => (
              <div onClick={() => this.toggleLightbox(j)} key={thumbnail}>
              <img vertical-align="bottom" width= "100%" className="thumbnail shadow-lg m-2" src={thumbnail} srl_gallery_image="true" alt={desc}  />
              <figcaption className="figure-caption text-right">{desc}</figcaption>
              </div>
            ))}
            </Masonry>
            </ResponsiveMasonry>
        ) : null}

        <ModalGateway>
          {lightboxIsOpen && !isLoading ? (
            <Modal onClose={this.toggleLightbox}>
            <Carousel
              currentIndex={selectedIndex}
              views={imagesCollection.map(x => ({
                source: {"regular": x.image, "thumbnail": x.thumbnail, "fullscreen": x.image},
                caption: <div className='caption'>{x.desc}</div>,
                alt: x.desc
              }))}
            />
            </Modal>
          ) : null}
        </ModalGateway>
      </Fragment>
    )
  }
}
